'use client';

import Link from 'next/link';
import { useState, useEffect, useRef } from 'react';
import { useModal } from '@/hooks/useModal';

import styled from 'styled-components';
import Logo from '@/components/common/Logo';
import Button from '@/components/common/Button';
import Modal from '@/components/common/Modal';
import Dropdown from '@/components/common/Dropdown';
import Navigation from '@/components/header/Navigation';
import Drawer from '@/components/header/Drawer';
import ThemeSwitcher from '@/components/header/ThemeSwitcher';
import { FaUserCircle } from 'react-icons/fa';

const Header = () => {
	const { isOpen, modalType, openModal, closeModal } = useModal();
	const [isLogin, setIsLogin] = useState(false);
	const toggleLogin = () => {
		setIsLogin(!isLogin);
	};

	const [isHeaderFolded, setIsHeaderFolded] = useState(true);
	const lastScrollY = useRef(0);

	useEffect(() => {
		if (typeof window !== 'undefined') {
			const handleScroll = () => {
				const container = document.querySelector('body');
				const currentScrollY = container?.scrollTop || window.scrollY || 0;

				if (currentScrollY > lastScrollY.current) {
					setIsHeaderFolded(false);
				} else {
					setIsHeaderFolded(true);
				}

				lastScrollY.current = currentScrollY;
			};

			window.addEventListener('scroll', handleScroll);

			return () => {
				window.removeEventListener('scroll', handleScroll);
			};
		}
	}, []);

	return (
		<StyledHeader $isFolded={isHeaderFolded}>
			<div className="header">
				<div className="left-section">
					<Drawer />
					<Logo />
				</div>
				<Navigation />
				<div className="right-section">
					<ThemeSwitcher scheme="secondary" className={isLogin ? 'hidden' : 'mobile-hidden'} />
					{isLogin ? (
						<Dropdown
							className="auth"
							toggleButton={
								<Button>
									<FaUserCircle className="userCircle" />
								</Button>
							}
						>
							<>
								<Button className="item">
									<Link href="/myPage">마이페이지</Link>
								</Button>
								<Button className="item" onClick={toggleLogin}>
									로그아웃
								</Button>
							</>
						</Dropdown>
					) : (
						<>
							<Button scheme="secondary" onClick={toggleLogin} style={{ width: '5rem' }}>
								로그인
							</Button>
							<Button scheme="primary" style={{ width: '5rem' }} onClick={() => openModal('subscribe')}>
								구독하기
							</Button>
						</>
					)}
				</div>
			</div>
			{isOpen && modalType === 'subscribe' && (
				<Modal isOpen={isOpen} onClose={closeModal}>
					<div>구독하기 모달 내용</div>
				</Modal>
			)}
		</StyledHeader>
	);
};

interface StyledProps {
	$isFolded: boolean;
}

const StyledHeader = styled.header<StyledProps>`
	position: fixed;
	top: ${({ $isFolded }) => ($isFolded ? '0' : '-3rem')};
	left: 0;
	z-index: 1000;

	width: 100%;
	margin: 0;
	padding: 0;
	transition: top 0.3s ease;

	background: ${({ theme }) => theme.color.background};
	border-bottom: 1px solid ${({ theme }) => theme.color.border};

	.header {
		position: relative;
		background: ${({ theme }) => theme.color.background};
		z-index: 1000;

		display: flex;
		flex-direction: row;
		justify-content: space-between;
		align-items: center;

		margin: 0 auto;
		gap: 1rem;

		width: 100%;
		height: 3rem;
		max-width: ${({ theme }) => theme.layout.width.large};
	}

	.left-section {
		position: relative;
		left: 0;
		width: fit-content;
		height: 100%;
		display: flex;
		flex-direction: row;
		justify-content: flex-start;
		align-items: center;
	}

	.right-section {
		position: absolute;
		right: 0;
		width: fit-content;
		height: 100%;
		display: flex;
		flex-direction: row;
		justify-content: flex-end;
		align-items: center;
		gap: 0.5rem;

		.userCircle {
			border-radius: ${({ theme }) => theme.borderRadius.circle};
			object-fit: cover;
		}
	}

	.hidden {
		visibility: hidden;
	}

	.mobile-hidden {
		@media ${({ theme }) => theme.mediaQuery.tablet} {
			visibility: hidden;
		}
	}

	.desktop-hidden {
		visibility: hidden;

		@media ${({ theme }) => theme.mediaQuery.tablet} {
			visibility: visible;
		}
	}
`;

export default Header;
