import { styled } from 'styled-components';
import { NAVIGATION } from '@/constants/navigation';
import Link from 'next/link';
import Button from '@/components/common/Button';
import SubNavigation from '@/components/header/SubNavigation';
import { useState } from 'react';

const Navigation = () => {
	const [visibleSubNav, setVisibleSubNav] = useState<number | null>(null);

	const handleMouseEnter = (index: number) => {
		setVisibleSubNav(index);
	};

	const handleMouseLeave = () => {
		setVisibleSubNav(null);
	};

	return (
		<StyledNavigation>
			<ul>
				{NAVIGATION.map((item, index) => (
					<li key={index} onMouseEnter={() => handleMouseEnter(index)} onMouseLeave={handleMouseLeave}>
						<Link href={item.link} passHref>
							<Button>{item.title}</Button>
						</Link>
						{item.subItems && <SubNavigation items={item.subItems} isActive={visibleSubNav === index} />}
					</li>
				))}
			</ul>
		</StyledNavigation>
	);
};

const StyledNavigation = styled.nav`
	width: 100%;
	height: 100%;
	display: flex;
	flex-direction: row;
	align-items: center;
	justify-content: flex-start;

	white-space: nowrap;
	transform: scaleX(1);
	transform-origin: left;
	transition: transform 0.3s ease, visibility 0.3s ease;

	ul {
		height: 100%;
		display: flex;
		flex-direction: row;
		align-items: center;
		justify-content: flex-start;
		padding: 0;
		margin: 0;
		gap: 1rem;
		line-height: 1;

		li {
			padding: 0.5rem 0;
			margin: 0;
			cursor: pointer;
			font-size: ${({ theme }) => theme.fontSize.small};

			&:hover {
				box-shadow: inset 0px -2px 0px ${({ theme }) => theme.color.primary};
			}
		}
	}

	@media ${({ theme }) => theme.mediaQuery.tablet} {
		visibility: hidden;
		transform: scaleX(0);
	}
`;

export default Navigation;
