import Link from 'next/link';
import { useEffect } from 'react';
import { createPortal } from 'react-dom';
import useMountStore from '@/store/mountStore';
import Button from '@/components/common/Button';
import Dropdown from '@/components/common/Dropdown';

interface Props {
	items: { link: string; title: string }[];
	isActive: boolean;
}

const SubNavigation = ({ items, isActive }: Props) => {
	const isMounted = useMountStore((state) => state.isMounted);
	const setMounted = useMountStore((state) => state.setMounted);

	useEffect(() => {
		setMounted();
	}, [setMounted]);

	if (!isMounted) return null;

	return createPortal(
		<Dropdown className={`sub-navigation ${isActive ? 'active' : ''}`}>
			{items.map((subItem, index) => (
				<Link href={subItem.link} key={index} className="item">
					<Button>{subItem.title}</Button>
				</Link>
			))}
		</Dropdown>,
		document.body
	);
};

export default SubNavigation;
