'use client';

import React, { useEffect, useRef, useState } from 'react';
import styled from 'styled-components';

interface Props {
	children: React.ReactNode;
	toggleButton?: React.ReactNode;
	isOpen?: boolean;
	className?: string;
}

const Dropdown = ({ children, toggleButton, isOpen = false, className }: Props) => {
	const [open, setOpen] = useState(isOpen);
	const dropdownRef = useRef<HTMLDivElement>(null);

	const [lastScrollY, setLastScrollY] = useState(0);

	useEffect(() => {
		const handleScroll = () => {
			const currentScrollY = window.scrollY;

			if (currentScrollY > lastScrollY) {
				setOpen(false);
			}

			setLastScrollY(currentScrollY);
		};

		window.addEventListener('scroll', handleScroll);

		return () => {
			window.removeEventListener('scroll', handleScroll);
		};
	}, [lastScrollY]);

	useEffect(() => {
		function handleOutsideClick(event: MouseEvent) {
			if (dropdownRef.current && !dropdownRef.current.contains(event.target as Node)) {
				setOpen(false);
			}
		}

		document.addEventListener('mousedown', handleOutsideClick);

		return () => {
			document.removeEventListener('mousedown', handleOutsideClick);
		};
	}, []);

	return (
		<StyledDropdown $open={open} ref={dropdownRef} className={`${className}`}>
			<div className="toggle-button" onClick={() => setOpen(!open)}>
				{toggleButton &&
					React.isValidElement(toggleButton) &&
					(toggleButton.type !== React.Fragment
						? React.cloneElement(toggleButton as React.ReactElement<React.HTMLAttributes<HTMLDivElement>>, {
								className: open ? 'open' : '',
						  })
						: toggleButton)}
			</div>
			<div className="panel" onClick={() => setOpen(false)}>
				{children}
			</div>
		</StyledDropdown>
	);
};

